.Icon,
.Logo {
  height: 1em;
  width: auto;
  padding: 0.02em;
  fill: currentcolor; }
  .Icon.fallback,
  .Logo.fallback {
    display: inline;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 700;
    vertical-align: text-top;
    font-size: 0.4em;
    height: 0.5em;
    padding: 0.1em 0.3em;
    margin: 0.1em 0;
    color: currentcolor; }



